.library-body {
    height: 94%;
    width: 94%;
    padding: 3%;
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
    justify-content: space-between;
}

.playlist-card {
    position: relative;
    width: 20%;
    height: 55%;
    border-radius: 20px;
    background-color: rgb(30, 42, 62);
    border: 1px solid rgba(54, 69, 98, 0.18);
    padding: 1%;
    margin-bottom: 2%;
    background: rgba(30, 42, 62);
    background: linear-gradient(75deg,
            rgb(40, 58, 88)0%,
            rgba(54, 69, 98, 0)100%);
    cursor: pointer;
}

.playlist-card:hover {
    transform: scale(1.02);
    transition: all 0.5s ease;
}

.playlist-card:hover .playlist-fade {
    opacity: 1;
}

.playlist-image {
    width: 100%;
    aspect-ratio: 1;
    border-radius: 15px;
}

.playlist-title {
    font-weight: 800;
    display: -webkit-box;
    overflow: hidden;
    font-size: 16px;
    color: #c4d0c3;
    margin: 10px 0 10px;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}

.playlist-subtitle {
    font-weight: 400;
    font-size: 12px;
    margin: 0px;
    color: #c4d0e37c;
}

.playlist-fade {
    position: absolute;
    right: 0;
    bottom: 0;
    opacity: 0;
    width: 84%;
    height: 34%;
    border-radius: 20px;
    background: linear-gradient(188deg,
            rgb(54, 69, 98, 0) 10%,
            rgba(54, 69, 98, 1)100%);
    display: flex;
    align-items: flex-end;
    justify-content: flex-end !important;
    padding-bottom: 10%;
    transition: 0.5s ease;
}